.contacts {
    min-height: 60vh;
    background-color: #242323;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.insideBorder{
    border: 1px solid;
    width: 80%;
    height: 45vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    justify-content: space-around;
}
.containerContacts{
    display: flex;
    flex-direction: column;
    border: 1px solid;
    width: 50%;
    margin:  10px;

}
.textarea{
    height: 150px;
}
.button{

}
.span{
    background-color: antiquewhite;
}
@media screen and (max-width: 670px){
    .containerContacts{
        width: 80%;
    }
}
@media screen and (max-width: 400px){
    .containerContacts{
        width: 100%;
    }
}